import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

import styles from "./Reviews.module.css";

class ReviewDetails extends Component {
  _isMounted = false;
  constructor(props) {
    super(props);
    this.state = {
      details: "",
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this.getReview();
  }

  getReview() {
    let reviewId = this.props.match.params.id;
    axios
      .get(
        `http://localhost:3000/api/reviews/${reviewId}?access_token=${sessionStorage.getItem(
          "token"
        )}`
      )
      .then((response) => {
        if (this._isMounted) {
          this.setState({ details: response.data });
        }
      })
      .catch((err) => console.log(err));
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    return (
      <div className={styles.container}>
        <h1 className={styles.title}>{this.state.details.title}</h1>
        <div className={styles.details}>
          <p className={styles.body}>{this.state.details.description}</p>
        </div>
        <h2 className={styles.score}>{this.state.details.score}/10</h2>

        <Link to="/" className="button">
          Back
        </Link>
      </div>
    );
  }
}

export default ReviewDetails;
