import React, { Component } from "react";
import axios from "axios";
import { Link, Redirect } from "react-router-dom";

import styles from "./Reviews.module.css";
import ReviewItem from "./ReviewItem";

class Reviews extends Component {
  _isMounted = false;
  constructor() {
    super();
    this.state = {
      reviews: [],
    };
  }

  componentDidMount() {
    this._isMounted = true;
    if(sessionStorage.getItem("token") !== null || undefined){
      this.getReviews();
    }
  }

  getReviews() {
    axios
      .get(
        `http://localhost:3000/api/reviews?access_token=${sessionStorage.getItem(
          "token"
        )}`
      )
      .then((response) => {
        if (this._isMounted) {
          this.setState({ reviews: response.data });
        }
      })
      .catch((err) => console.log(err));
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    const reviewItem = this.state.reviews.map((review, i) => {
      return <ReviewItem key={review.id} item={review} />;
    });
    if (sessionStorage.getItem("token") === null || undefined) {
      return <Redirect to="/signup" />;
    }
    return (
      <div className={styles.container}>
        <h1 className={styles.title}>Reviews</h1>
        <div className={styles.reviews}>{reviewItem}</div>
        <div>
          <Link to="/reviews/add" className="button">
            Add Review
          </Link>
        </div>
      </div>
    );
  }
}

export default Reviews;
